/*
<label class="label-checkbox">
    <input type="checkbox" class="checkbox-custom">
    <span class="checkbox-custom-view">для вязки</span>
</label>
*/
.checkbox-custom {
  display: none;
}

.checkbox-custom:checked + .checkbox-custom-view:before {
  border-color: #1497a7;
}

.checkbox-custom:checked + .checkbox-custom-view:after {
  visibility: visible;
}

.checkbox-custom-view {
  padding-left: 20px;
  cursor: pointer;
  position: relative;
  display: inline-flex;
  line-height: 1;
  align-items: center;
  flex-wrap: wrap;
}

.checkbox-custom-view:before {
  width: 15px;
  height: 15px;
  margin-right: 15px;
  top: 0;
  left: 0;
  position: absolute;
  background-color: #ffffff;
  content: '';
  border: 1px solid #1497a7;
  transition: all 0.3s ease;
}

.checkbox-custom-view:after {
  width: 10px;
  height: 5px;
  top: 4px;
  left: 3px;
  position: absolute;
  border-bottom: 2px solid #1497a7;
  border-left: 2px solid #1497a7;
  content: '';
  visibility: hidden;
  transform: rotate(-45deg);
}

.checkbox-custom-view a {
  text-decoration: underline;
}

.checkbox-custom-view a:hover {
  text-decoration: none;
}

.checkbox-custom-view a, .checkbox-custom-view span {
  padding-left: 5px;
}

